/* Admin.css - 이 파일을 만들고 App.css 아래에 import 하세요 */

/* 관리자 레이아웃 */
.admin-layout {
  display: flex;
  min-height: calc(100vh - 60px);
  margin-top: 200px;
}
.admin-nav{
  display:flex;
  gap:20px;
  background-color: #2c3e50;
  padding: 10px 20px;
  color: white;
  height:80px;
  align-items: center;
  margin-top: -20px;
}

.admin-sidebar {
  width: 250px;
  background-color: #2c3e50;
  color: white;
  padding: 20px;
}

.admin-sidebar h3 {
  margin-bottom: 20px;
  padding-bottom: 10px;
  border-bottom: 1px solid #34495e;
}

.admin-sidebar ul {
  list-style: none;
  padding: 0;
}

.admin-sidebar ul li {
  margin-bottom: 10px;
}

.admin-sidebar ul li a {
  color: white;
  text-decoration: none;
  display: block;
  padding: 8px 10px;
  border-radius: 4px;
}

.admin-sidebar ul li a:hover {
  background-color: #34495e;
}

.admin-content {
  flex: 1;
  padding: 20px;
}

/* 대시보드 카드 */
.dashboard-stats {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin-top: 20px;
}

.stat-card {
  background-color: white;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0,0,0,0.1);
  padding: 20px;
  flex: 1;
  min-width: 200px;
}

.stat-card h3, .stat-card h4 {
  margin-top: 0;
  color: #2c3e50;
}

.stat-number {
  font-size: 2rem;
  font-weight: bold;
  color: #3498db;
}

/* 테이블 스타일 */
table.admin_table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

table.admin_table th, table.admin_table td {
  border: 1px solid #ddd;
  padding: 12px;
  text-align: left;
}

table.admin_table th {
  background-color: #f2f2f2;
  font-weight: bold;
}

table.admin_table tr:nth-child(even) {
  background-color: #f9f9f9;
}

/* 폼 스타일 */
.add-video-form {
  background-color: #f9f9f9;
  padding: 20px;
  border-radius: 8px;
  margin-bottom: 30px;
}

.form-group {
  margin-bottom: 15px;
}

.form-group label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
}

.form-group input, .form-group select {
  width: 100%;
  padding: 8px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

button {
  background-color: #3498db;
  color: white;
  border: none;
  padding: 8px 15px;
  border-radius: 4px;
  cursor: pointer;
  margin-right: 5px;
}

button:hover {
  background-color: #2980b9;
}

button.delete-btn {
  background-color: #e74c3c;
}

button.delete-btn:hover {
  background-color: #c0392b;
}