html,body {
   position: relative;
   font-size:16px;
   box-sizing: border-box;
}
body {
   /* width: 1440px; */
   max-width: 1440px;
   margin:0 auto;
   background-color: #3b3e38;
   border:1px solid black;
}

iframe{
   width:100%;
   border:none;
}
input{
   border: none;
}
.confirm{
   font-size: 16px;
   width: 100px;
   height: 100%;
   border-radius: 20px;
   box-sizing: border-box;
   font-weight:600;
   cursor: pointer;
   color: white;
   background-color: rgb(189, 48, 48, 0.4);
   transition: background 0.3s;
}
input.confirm:hover {
  background-color: rgb(189, 48, 48, 0.9) ;
}
.sectionClass{
   margin-top: 150px;
   color: white;
   /* font-size: 1.5rem; */
}
.title {
  position:fixed;
  top:90px;
  width:1440px;
  height:60px;
  background-color: #3b3e38;
  z-index: 100;
  display: flex;
  align-items: center;
}
.title h3{
   /* position:fixed; */
   /* top:50px; */
   color: white;
   font-size: 1.5rem;   
   max-width:1440px;
   height:40px;
   background-color: #3b3e38;
   z-index: 3;
   margin:0;
   padding-top:0px;
   padding-left: 10px;
}

