/* UserManagement.css */
.user-management {
  padding: 20px;
}

.user-management h2 {
  margin-bottom: 20px;
}

.search-bar {
  margin-bottom: 20px;
}

.search-bar input {
  width: 100%;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.user-list table {
  width: 100%;
  border-collapse: collapse;
}

.user-list th, 
.user-list td {
  padding: 12px 15px;
  border-bottom: 1px solid #ddd;
  text-align: left;
}

.user-list th {
  background-color: #f8f8f8;
  cursor: pointer;
  user-select: none;
}

.user-list th:hover {
  background-color: #f0f0f0;
}

.delete-btn {
  padding: 5px 10px;
  background-color: #f44336;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.delete-btn:hover {
  background-color: #d32f2f;
}