/* VideoManagement.css */
.video-management {
  padding: 20px;
}

.video-management h2 {
  margin-bottom: 20px;
}

.add-video-form {
  background-color: #f5f5f5;
  padding: 20px;
  border-radius: 8px;
  margin-bottom: 30px;
}

.form-row {
  display: flex;
  gap: 20px;
  margin-bottom: 15px;
}

.form-group {
  margin-bottom: 15px;
  flex: 1;
}

.form-group label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
}

.form-group input,
.form-group select,
.form-group textarea {
  width: 100%;
  padding: 8px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.form-buttons {
  display: flex;
  gap: 10px;
  margin-top: 20px;
}

.form-buttons button {
  padding: 8px 16px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.form-buttons button:hover {
  background-color: #45a049;
}

.form-buttons button:nth-child(2) {
  background-color: #f44336;
}

.video-list {
  margin-top: 30px;
}

.video-list table {
  width: 100%;
  border-collapse: collapse;
}

.video-list th, 
.video-list td {
  padding: 10px;
  border: 1px solid #ddd;
  text-align: left;
}

.video-list th {
  background-color: #f2f2f2;
}

.video-list button {
  margin-right: 5px;
  padding: 5px 10px;
  cursor: pointer;
}