@font-face {
    font-family: 'NanumBarunGothic';
    src: url('//cdn.jsdelivr.net/font-nanumlight/1.0/NanumBarunGothicWeb.eot');
    font-weight: 400;
    font-display: swap;
}
*{
   margin:0;padding:0;
   font-size:20px;
}
header{
   width:100%;
   height:90px;
   max-width: 1440px;
   position: fixed;
   top: 0px;
   background-color: #3b3e38;
   z-index:4;
}
.header_group {
  position: fixed;
  width:100%;
  height:0px;
  max-width: 1440px;
  top:100px;
  background-color: #3b3e38;
  z-index:4;
}
.top{
   font-family: 'NanumBarunGothic', sans-serif;
   width:100%;
   max-width: 1440px;
}
.top .top_menu{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding:2px 10px;
  gap: 10px;
}
.m_logo{
   display: flex;
}
.m_logo img{
   width:100%;
   min-width: 70px;
   height: 80px;
   border-radius: 18px;
}
.genre{
   width:50%;
   min-width:50px;
   height:50px;
   font-family: 'NanumBarunGothic', sans-serif;
}
.top1{
   display: flex;
   align-items: center;
   width:30%;
   gap:20px;
}
.top2{
   display: flex;
   align-items: center;
}
.search{
  display: flex;
  height: 30px;
  align-items: center;
}
input.input{width: 170px; height:100%;}
button.s_click{
   width:70px;
   height:30px;
   display: flex;            /* 이 부분을 추가해주세요! */
   justify-content: center;  /* 수평 중앙 정렬 */
   align-items: center;      /* 수직 중앙 정렬 (이제 작동합니다!) */
   background-color: rgb(189, 48, 48);
   color: white;
   font-size:20px;
   transition: all 0.3s;
   font-family: 'NanumBarunGothic', sans-serif;
   border: none; /* 혹시 모를 기본 테두리 제거 */
   cursor: pointer; /* 마우스 오버 시 포인터 변경 */
}
.s_click:hover{
   /* font-weight:bolder; */
   background-color: rgb(197, 86, 86); 
   transform:scale(1.1);
}
ul{
   display: flex;
   list-style: none;
   align-items: center;
   gap: 40px;
}
.main_links li{
   transition: all 0.2s;
}
.res_dropdown li{
   transition: all 0.2s;
}
.res_dropdown2 li{
   width:150px;
   transition: all 0.2s;
}
.res_dropdown2 li:hover a, .res_dropdown2 li:hover{
   transform:scale(1.05);
   color:rgb(254, 199, 0);
}
.res_dropdown li:hover a, .res_dropdown li:hover{
   transform:scale(1.1);
   color:rgb(254, 199, 0);
}
a{
   text-decoration: none;
   color:white;
}
.res_dropdown2, .down{
   display: none;
}
.down img{
   display:none;
   width:32px;
   background-color: rgba(255, 255, 255, 0.5);
}
@media (max-width:1295px){
   .res_dropdown{
      display: none;
   }
   #res_menu, .down{
      display:flex;
   }
}
.down:hover .res_dropdown2{
   display: block;
} 
.down:hover #res_menu{
   display:none;
}